<script>
import { mapActions, mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'
import VueNumeric from 'vue-numeric'
export default {
  data: function () {
    return {
      errors: []
    }
  },
  components: {
    Datepicker,
    VueNumeric
  },
  methods: {
    ...mapActions([
      'getEmployee',
      'resetPayrollVar',
      'setEmployeeFormDisplay',
      'saveEmployee',
      'updateEmployee'
    ]),
    checkForm: function (e) {
      console.log('checking form data')
      this.errors = []
      if (!this.employee.empName) {
        this.errors.push('Name Required.')
      }
      if (this.employee.empName.length > 40) {
        this.errors.push('Name should not be more than 40 chr')
      }
      if (this.employee.empName.length < 3) {
        this.errors.push('Name should be at least 3 chr')
      }
      if (!this.employee.deptCode) {
        this.errors.push('Department Required.')
      }
      if (!this.employee.cprNo) {
        this.errors.push('Employee CPR Required.')
      }
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Fields highlighted in yellow are required, please enter following ..'
        )
      }
    },
    houseKeeping () {
      this.errors = []
    },
    async save () {
      this.checkForm()
      if (!this.errors.length) {
        if (this.employee.id === 0) {
          await this.saveEmployee(this.employee)
        } else {
          await this.updateEmployee(this.employee)
        }
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      } else {
        this.$toasted.show(this.errors, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
      this.houseKeeping()
    }
  },
  computed: {
    ...mapGetters(['employee', 'message'])

  }
}
</script>

