<template>
  <div class="main-div">
     <div id="showformid" v-show="showFormEmployeeMaster">
      <masterForm />
    </div>
    <!-- {{this.totalRows}} -->
    <div id="showformlistid" v-show="showFormEmployeeList">
      <br>
      <b-row>
      <b-col cols="1">
        <b-button squared variant="secondary" @click="addEmployee()">Add New</b-button>
      </b-col>
      <!-- <b-col cols="1">
        <button class="button" @click="addEmployee()"> Add new </button>
      </b-col> -->
      <div class="center">
      <h5 class="center">Employee List</h5>
      </div>
      </b-row>
    <hr />
      <b-col cols="11" offset="1" >
        <b-row>
          <div>
            <b-container fluid>
              <!-- User Interface controls -->
              <b-row>
                <b-col cols="6" class="my-1">
                  <b-form-group
                    label="Filter"
                    label-for="filter-input"
                    label-cols-sm="1"
                    label-align-sm="right"
                    label-size="sm"
                    class="mb-0"
                  >
                    <b-input-group size="sm">
                      <b-form-input
                        id="filter-input"
                        v-model="filter"
                        type="search"
                        placeholder="Type to Search"
                      ></b-form-input>

                      <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''"
                          >Clear</b-button
                        >
                      </b-input-group-append>
                    </b-input-group>
                  </b-form-group>
                </b-col>

                <b-col cols="6" class="my-1">
                  <b-form-group
                    v-model="sortDirection"
                    label="Filter On"
                    description="Leave all unchecked to filter on all data"
                    label-cols-sm="3"
                    label-align-sm="right"
                    label-size="sm"
                    class="mb-0"
                    v-slot="{ ariaDescribedby }"
                  >
                    <b-form-checkbox-group
                      v-model="filterOn"
                      :aria-describedby="ariaDescribedby"
                      class="mt-1"
                    >
                      <b-form-checkbox value="empCode"
                        >Employee Code</b-form-checkbox
                      >
                      <b-form-checkbox value="empName"
                        >Employee Name</b-form-checkbox
                      >
                      <b-form-checkbox value="cprNo"
                        >CPR</b-form-checkbox
                      >
                       <b-form-checkbox value="profession"
                        >Profession</b-form-checkbox
                      >
                      <!-- <b-form-checkbox value="isActive">Active</b-form-checkbox> -->
                    </b-form-checkbox-group>
                  </b-form-group>
                </b-col>
              </b-row>
              <b-row>
                <b-col cols="6" class="my-1">
                  <b-form-group
                    label="Per page"
                    label-for="per-page-select"
                    label-cols-sm="2"
                    label-align-sm="left"
                    label-size="sm"
                    class="mb-0"
                  >
                    <b-form-select
                      id="per-page-select"
                      v-model="perPage"
                      :options="pageOptions"
                      size="sm"
                    ></b-form-select>
                  </b-form-group>
                </b-col>

                <b-col sm="7" md="6" class="my-1">
                  <b-pagination
                    v-model="currentPage"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    align="fill"
                    size="sm"
                    class="my-0"
                  ></b-pagination>
                </b-col>
              </b-row>

              <!-- Main table element -->
              <b-table
                hover
                striped
                head-variant="light"
                table-class="w3-table w3-striped"
                selectable
                :select-mode="selectMode"
                selected-variant="warning"
                :items="employeeList"
                :fields="fields"
                :current-page="currentPage"
                :per-page="perPage"
                :filter="filter"
                :filter-included-fields="filterOn"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :sort-direction="sortDirection"
                stacked="md"
                show-empty
                small
                @filtered="onFiltered"
              >
                <template #table-colgroup="scope">
                  <col
                    v-for="field in scope.fields"
                    :key="field.key"
                    :style="{
                      width: field.key === 'empName' ? '400px' : '300px',
                    }"
                  />
                </template>
                <template #cell(name)="row">
                  {{ row.value.first }} {{ row.value.last }}
                </template>
                <template #cell(actions)="row">
                  <b-button size="sm" @click="showDetails(row.item)">
                    {{ row.detailsShowing ? "Hide" : "Show" }} Details
                  </b-button>
                </template>
                <template #row-details="row">
                  <b-card>
                    <ul>
                      <li v-for="(value, key) in row.item" :key="key">
                        {{ key }}: {{ value }}
                      </li>
                    </ul>
                  </b-card>
                </template>
              </b-table>
            </b-container>
          </div>
        </b-row>
      </b-col>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'
import masterForm from './EmployeeMaster.vue'
export default {
  data () {
    return {
      empId: {},
      visibleRows: [],
      leaveSummaryList: [],
      currentPage: 1,
      perPage: 5,
      // totalRows: items.length,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      // sortDirection: "asc",
      filter: '',
      filterOn: [],
      sortDirection: 'asc',
      leaveHistoryList: [],
      fields: [
        {
          key: 'id',
          label: 'Code',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'empName',
          label: 'Name',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'empName',
          label: 'Name',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'cprNo',
          label: 'CPR',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'profession',
          label: 'Profession',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'Nationality',
          label: 'nationality',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'onBoardingDate',
          label: 'Date of Join',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        { key: 'actions', label: 'Actions' }
      ]
    }
  },
  mounted () {
    this.fetchEmployeeList()
  },
  components: {
    masterForm
    // VueNumeric
  },
  computed: {
    ...mapGetters([
      'message',
      'employeeList',
      'fetchStartRec',
      // 'showForm',
      // 'showFormList',
      'showFormEmployeeList',
      'showFormEmployeeMaster',
      'employee',
      'totalRows'
    ])
  },
  methods: {
    ...mapActions([
      'fetchEmployeeList',
      'setEmployeeFormDisplay',
      'defaultState',
      'setIsEditMode',
      'fetchEmployee',
      'fetchStartRec'
    ]),
    showDetails: function (x) {
      // this.setFormDisplay(true)
      this.setEmployeeFormDisplay()
      this.setIsEditMode(false)
      this.fetchEmployee(x.id)
    },
    addEmployee: function () {
      console.log('opening new form' + this.showForm)
      this.defaultState()
      this.setIsEditMode(true)
      this.setEmployeeFormDisplay()
      // this.setFormDisplay(true)
    },
    editEmployee () {
      this.fetchEmployee(this.employee.id)
      this.fetchLeaveHistoryList(this.employee.id)
      this.setEmployeeFormDisplay()
    },
    formatData (value, x) {
      if (!value) return ''
      if (x.includes('date')) {
        // console.log('col name includes date')
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else if (x.includes('Date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
        // return value
      } else {
        // console.log('not formated')
        return value
      }
    }
  }
}
</script>
